import React, { Component } from "react";
import { Divider, Icon } from "semantic-ui-react";
import styles from "../styles/style";
import loadable from "@loadable/component";

class Index extends Component {
  state = {
    isDownload: false
  };
  handleIsDownload = () => {
    this.setState({ isDownload: !this.state.isDownload });
  };
  render() {
    const { isDownload } = this.state;
    return (
      <div id="divToPrint">
        <p style={styles.h2Style} id="home" />
        <Headers />
        <AboutMe
          isDownload={isDownload}
          handleIsDownload={this.handleIsDownload}
        />

        <p style={styles.h2Style} id="me" />
        <Me isDownload={isDownload} handleIsDownload={this.handleIsDownload} />

        <p style={styles.h2Style} id="experiences" />
        <Experiences />

        <p style={styles.h2Style} id="skillset" />
        <Divider horizontal>
          <h1>
            <Icon name="wrench" />
            Skillset
          </h1>
        </Divider>
        <br />
        <Skill />
        <br />
        <Divider />

        <p style={styles.h2Style} id="contact" />
        <Contact />
        <Divider />
        <Footer />
      </div>
    );
  }
}

export default Index;

const Headers = loadable(() => import("../components/header"));
const Me = loadable(() => import("../components/me"));
const Footer = loadable(() => import("../components/footer"));
const Skill = loadable(() => import("../components/skillSet"));
const AboutMe = loadable(() => import("../components/aboutMe"));
const Experiences = loadable(() => import("../components/experiences"));
const Contact = loadable(() => import("../components/contact"));


